p {
    text-align: left;
    font-family: Verdana;
    color: black;
    font-size: 15px;
}

h2 {
    text-align: left;
    font-family: Georgia;
    color: black;
    font-size: 18px;
}

li {
    text-align: left;
    font-family: Verdana;
    color: black;
    font-size: 15px;
}

.PriceList-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
}

.PriceList-item {
    background-color: #f0f0f0;
    padding: 20px;
}

@media screen and (max-width: 768px) {
    .PriceList-container {
        grid-template-columns: 1fr;
    }
}

.bottom-text {
    background-color: #f0f0f0;
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 10px;
    padding-right: 3px;
    text-align: center;
    max-width: 800px;
    margin: 0 auto;
}