.black-background {
    text-align: center;
}

.photo-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
}

.photo-grid img {
    max-width: 100%;
    height: auto;
}

p {
    text-align: left;
    font-family: Verdana;
    color: black;
    font-size: 16px;
}