.image-container {
    position: relative;
    display: inline-block;
}

.image {
    width: 300px;
    height: auto;
}

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 98%;
    opacity: 0;
    background-color: rgba(0, 0, 0, 0.7);
    transition: opacity 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overlay:hover {
    opacity: 1;
}

.text {
    color: white;
    font-size: 20px;
    text-align: center;
    padding: 20px;
    font-family: Georgia;
}