header {
    padding: 20px;
}

nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav .left-section ul,
nav .right-section ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin-right: 15px;
}

nav ul li a {
    color: black;
    text-decoration: none;
    font-family: Georgia;
    font-size: 18px;
}

nav .left-section ul {
    font-family: Georgia;
    font-size: 20px;
    color: black;
    text-decoration: none;
    font-weight: 400;
}

nav .left-section ul li:hover {
    transform: scale(1.05);
    transition: 0.2s;
    color: black;
    font-weight: 500;
    background-color: rgb(247, 247, 247);
    border-radius: 5px;
}

nav .right-section ul li:hover {
    transform: scale(1.1);
    transition: 0.2s;
}

nav .left-section ul li,
nav .right-section ul li {
    margin-right: 10px;
}

nav .left-section ul li:last-child,
nav .right-section ul li:last-child {
    margin-right: 0;
}

nav .center-section {
    flex-grow: 1;
    text-align: center;
}

.logo {
    max-width: 100px;
}

.nav-buttons {
    width: 30px;
}

/* Media query for smaller screens */
@media screen and (max-width: 768px) {
    nav .left-section ul {
        font-size: 16px; /* Decrease font size for smaller screens */
    }

    .logo {
        max-width: 80px; /* Decrease logo size for smaller screens */
    }

    .nav-buttons {
        width: 20px; /* Decrease size of social media icons for smaller screens */
    }
}